import axios from 'axios'
import qs from 'qs';

export default class Client {

  async getBanks() {
    // TODO add try catch, error response
    const url = window.bridge.bank_list_url;

    const response = await axios.post(url, qs.stringify({
      action: 'GetBanksList',
      ajax: true
    }));

    return response.data;
  }

  async getPaymentUrl(actionUrl, idBank) {
    // TODO  add try catch / error response
    const response = await axios.post(actionUrl, qs.stringify({
      action: 'GetPaymentId',
      ajax: true,
      idBank: idBank,
    }));

    return response.data;
  }
}