<template>
  <div :class="{'btn btn-primary': isSelected, 'bank-wrapper': isSelected == false}"
       @click="onBankSelect($event)">
    <div class="bank-image-wrapper">
      <img class="bank-image" :src="bank.logo" :title="bank.name" :alt="bank.name"/>
    </div>
    <div class="bank-name pl-2" v-html="bankWithBlankLine"></div>
  </div>
</template>

<script>
import {paymentEmitter} from "../../events/emitters";
import {ON_BANK_SELECT} from "../../events/EventType";

export default {
  props: {
    bank: Object,
    isSelected: Boolean
  },
  methods: {
    onBankSelect($event) {
      $event.preventDefault();
      $event.stopPropagation();
      paymentEmitter.$emit(ON_BANK_SELECT, this.bank);
    }
  },
  computed: {
    bankWithBlankLine: function () {
      return this.bank.name.split('(').join('<br />(');
    }
  }
}
</script>

