<template>
  <div id="bridge-payment" class="w-100">
    <input type="hidden"
           :value="getIdBankSelected">
    <div id="bridge-banklist" class="p-2">
      <div class="bridge-pay-title m-1" v-text="translations.choose_bank"></div>
      <div class="input-wrapper">
        <button type="button"
                v-if="search.level > 0"
                @click="onBackHandle($event)"
                class="btn btn-primary btn-back"
                v-text="translations.back"></button>
        <input type="text"               
              class="form-control"
              :placeholder="translations.search_bank"
              v-model="search.text">
      </div>
      <div class="banks-wrapper mb-2">
        <Bank v-for="bank in searchBanks"
              :key="bank.name"
              :bank="bank"
              :is-selected="isBankSelected(bank)">
        </Bank>
        <div v-if="searchBanks.length <= 0">
          <div class="bank-wrapper">
            <i class="material-icons mi-settings">info_outline</i>  
            <div class="bank-name pl-2" v-html="translations.no_results_found"></div>
          </div>
        </div>
      </div>
      <div class="buttons-wrapper">
        <button type="button"
                class="btn btn-primary"
                @click="onValidatePayment"
                :class="{'disabled': !getIdBankSelected || !isConditionsApproved}"
                :disabled="!getIdBankSelected || !isConditionsApproved"
                v-text="translations.pay"></button>
      </div>
    </div>
    <div id="bridge-alerts" class="container">
    </div>
  </div>
</template>

<script>

import Client from '../../custom/Client';
import Bank from './Bank';
import {paymentEmitter} from '../../events/emitters';
import {ON_BANK_SELECT} from '../../events/EventType';

export default {
  name: 'Payment',
  components: {
    Bank
  },
  data() {
    return {
      banks: [],
      search: {
        text: '',
        searchArray: [],
        selected: null,
        level: 0
      },
      isConditionsApproved: false
    };
  },
  methods: {
    onBackHandle($event) {
      $event.preventDefault();
      $event.stopPropagation();
      this.search.selected = null;
      this.filterSearchArray(null);
    },
    filterSearchArray(selectedBank = null) {
      if (!selectedBank) {
        this.search.searchArray = this.banks;
        this.search.level = 0;
        this.search.text = '';
      } else if (selectedBank.children.length !== 0) {
        this.search.searchArray = selectedBank.children;
        this.search.level = 1;
        this.search.text = '';
      }
    },
    isBankSelected(bank) {
      if (!this.search.selected) {
        return false;
      }

      if (this.search.selected.children.length !== 0) {
        return false;
      }

      if (bank.children.length !== 0) {
        return false;
      }

      return bank.id_bank === this.search.selected.id_bank;
    },
    async onValidatePayment($event) {
      $event.preventDefault();
      $event.stopPropagation();      

      const form = document.getElementById('payment-form-bridge');
      const action =  form.action;
      const client = new Client();
      const url = await client.getPaymentUrl(action, this.getIdBankSelected);
      if (url.success) {
        window.location.href = url.message;
      } else {
        $('#bridge-alerts').html('<div class="alert alert-danger">' + url.message + '</div>');
      }
    },
    checkConditionsApproved() {
      const conditionsCheckbox = document.getElementById('conditions_to_approve[terms-and-conditions]');
      if (!conditionsCheckbox) {
        this.isConditionsApproved = true;
      } else {
        this.isConditionsApproved = conditionsCheckbox.checked;
      }
    }
  },
  computed: {
    searchBanks() {
      const self = this;

      return this.search.searchArray.filter(bank => {
        return bank.name.toLowerCase().includes(self.search.text.toLowerCase());
      });
    },
    getIdBankSelected() {
      if (!this.search.selected) {
        return null;
      }

      if (!this.search.selected.id_bank) {
        return null;
      }

      return this.search.selected.id_bank;
    },
    translations() {
      return window.bridge.translations;
    },

  },
  async created() {
    const self = this;

    this.checkConditionsApproved();

    const client = new Client();
    this.banks = await client.getBanks();
    this.filterSearchArray();

    paymentEmitter.$on(ON_BANK_SELECT, bank => {
      self.search.selected = bank;
      self.filterSearchArray(bank);
      self.checkConditionsApproved();
    });

    $(document).on('change', '[id="conditions_to_approve[terms-and-conditions]"]', () => {
      self.checkConditionsApproved();
    });
  }
}
</script>

